@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Xanh+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lateef&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Text:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background: #2b2b2b;
}
a {
    text-decoration: none;
    color: inherit;
}

.movie-owner {
    color: rgb(255, 0, 106);
}

.series-name {
    transition:all 0.5s;
}
.series-name:hover {
    color: rgb(179, 230, 245);
    font-size: 1.7em;
    transition:all .5s ease;
} 

.root-container {
    background: #2d2d2d;
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    overflow-x: hidden;
}
.app-container {
    width: 70%;
    height: 100%;
    min-height: 1024px;
    padding-bottom: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: auto;
}

.MuiBox-root {
    width: 100% !important;
}

.MuiTypography-root:focus {
    outline: none !important;
}

#vertical-tabpanel-0 {
    width: 100% !important;
    min-width: 525px;
}

.MuiTabs-root {
    max-width: 125px !important;
    min-width: 100px !important;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.MuiTab-wrapper {
    color: white;
    font-size: 1.5em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-transform: capitalize;
    letter-spacing: 0.05em;
}
.movie-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #383737b7;
    /* box-shadow: 0px 0px 1px rgb(70, 69, 69); */
    align-items: center;
}
.movie-container > * {
    padding: 20px 15px;
}

.movie-name-wrapper {
    min-width: 300px;
    width: 100%;
    color: rgb(8, 157, 243);
    font-size: 1em;

    font-family: "Roboto Slab", serif;
    letter-spacing: 0.05em;
    text-transform: capitalize;

    display: flex;
    justify-content: center;
    align-items: center;
}
.movie-year-wrapper {
    width: 100%px;
    min-width: 60px;
    color: tomato;
    text-align: center;
}

#is-watched {
    width: 100%;
    min-width: 100px;
    text-align: center;
}

.watched {
    color: rgb(235, 63, 11);
    font-weight: bolder;
}
.not-watched {
    color: rgb(11, 235, 197);
}

.download-link {
    color: rgb(192, 26, 170);
    transition: 0.5s;
}

.download-link a {
    text-decoration: none;
    color: white;
}
.download-link:hover {
    transform: translateY(-4px);
    transition: 0.5s;
    -webkit-filter: drop-shadow(2px 10px 5px rgb(17, 17, 17));
            filter: drop-shadow(2px 10px 5px rgb(17, 17, 17));
    background: linear-gradient(240.93deg, #f77740 5.44%, #de6ef1 95.58%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: all 0.3s ease;
}

.random-comp {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}

.imdb-wrapper {
    color: rgb(235, 231, 0);
}

.week-movie-container {
    width: 100%;
    height: 100%;
    border-radius: 1px;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-filter: drop-shadow(1px 55px 25px rgb(17, 17, 17));
            filter: drop-shadow(1px 55px 25px rgb(17, 17, 17));
    flex-direction: column;
}
.week-movie-wrapper {
    width: 90%;
}

.week-movie-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}
.week-movie-container img {
    /* width: 50%; */
    height: 60vh;
    border-radius: 20px;
    -webkit-filter: drop-shadow(2px 15px 15px rgb(17, 17, 17));
            filter: drop-shadow(2px 15px 15px rgb(17, 17, 17));
    margin: 0 10px;
}

.title-wrapper {
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgb(240, 230, 215);
    margin-bottom: 40px;
    font-family: "Xanh Mono", monospace;
    text-transform: capitalize;
    box-shadow: 0px 1px 15px 1px rgb(0, 0, 0);
    border-radius: 10px;
    padding: 10px;
}

.movie-name-btn {
    color: rgb(8, 157, 243);
    font-size: 1em;
    font-family: "Roboto Slab", serif;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    background-color: transparent;
    outline: none;
    border: none;
    cursor: pointer;
}

.movie-name-btn:hover {
    font-size: 1.1em;
}

@media (max-width: 1200px) {
    #vertical-tabpanel-0 {
        width: 100% !important;
        min-width: 400px !important;
    }
    .MuiBox-root {
        width: 95% !important;
    }

    .app-container {
        width: 98%;
    }
    .movie-container > * {
        padding: 20px 5px;
    }

    .movie-name-wrapper {
        min-width: auto;
    }
}

@media (max-width: 850px) {
    .movie-container > * {
        padding: 20px 0px;
    }
    .movie-name-wrapper {
        min-width: 0px;
    }
}

@media (max-width: 770px) {
    #vertical-tabpanel-0 {
        width: 100% !important;
        min-width: 400px !important;
    }
    .MuiBox-root {
        width: 95% !important;
    }

    .app-container {
        width: 99%;
    }

    .movie-name-wrapper {
        min-width: 110px;
    }
    .week-movie-container img {
        min-width: 50%;
    }
    .movie-container > * {
        font-size: 0.7em;
    }
}
@media (max-width: 540px) {
    #vertical-tabpanel-0 {
        width: 100% !important;
        min-width: 200px !important;
    }
    .MuiBox-root {
        width: 95% !important;
        padding: 10px !important;
    }
    .movie-container {
        width: 200px;
    }
    .app-container {
        width: 99.9%;
    }

    .movie-name-wrapper {
        min-width: 80px;
        font-size: 0.7em;
        padding: 20px 5px;
    }

    #is-watched {
        width: 20px;
        min-width: 100px;
        text-align: center;
        font-size: 0.8em;
        padding: 20px 5px;
    }
    .title-wrapper {
        font-size: 0.7em;
    }

    .week-movie-container img {
        min-width: 50%;
        height: 40vh;
    }
}
@media (max-width: 376px) {
    .title-wrapper{
        font-size: 0.5em;
    }
    #vertical-tabpanel-0 {
        width: 100% !important;
        min-width: 50px !important;
    }
    .MuiBox-root {
        width: 95% !important;
        padding: 10px !important;
    }
    .movie-container {
        width: 50%;
        overflow: hidden;
    }
    .app-container {
        width: 99.9%;
    }

    #is-watched {
        text-align: center;
        min-width: 90px;
        font-size: 0.5em;
        padding: 20px 0px;
    }
    .movie-container > * {
        font-size: 0.5em;
    }
    .movie-name-wrapper {
        min-width: 100px;
        font-size: 0.8em;
        padding: 20px 0px;
    }

    .movie-name-btn {
        padding: 0;
    }

    .MuiTabs-flexContainer > * {
        font-size: 0.5em !important;
    }
}

.MuiAccordionSummary-content {
    margin: 0 !important;
}

.MuiTypography-root {
    direction: rtl !important;
    letter-spacing: 0.02em !important;
    font-size: 1.5em !important;
    font-family: "Lateef", cursive;
    text-align: right !important;
}

.MuiAccordionDetails-root {
    padding: 10px 30px !important;
}

.random-container {
    background: #383838;
    width: 75%;
    height: 350px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-filter: drop-shadow(2px 15px 15px rgb(17, 17, 17));
            filter: drop-shadow(2px 15px 15px rgb(17, 17, 17));
    margin: 25px 0;
    flex-direction: column;
}
.choose-btn {
    padding: 10px;
    border-radius: 55px;
    width: 25%;
    color: rgb(255, 255, 255);
    background: black;
    border: 1px solid white;
    min-width: 55px;
    -webkit-filter: drop-shadow(2px 7px 5px rgb(17, 17, 17));
            filter: drop-shadow(2px 7px 5px rgb(17, 17, 17));
    margin-top: 80px;
    cursor: pointer;
    font-size: 1.2em;
    letter-spacing: 0.1em;
    transition: 0.5s;
}
.choose-btn:hover{
    transform: translateY(-8px);
    transition: .5s;
    background:linear-gradient(to bottom, rgb(0, 0, 0) ,rgb(19, 18, 18), rgba(41, 38, 38, 0.664));
    -webkit-filter: drop-shadow(0px 7px 10px rgb(17, 17, 17));
            filter: drop-shadow(0px 7px 10px rgb(17, 17, 17));

}
.choose-btn:focus {
    outline: none;
}

.chosen-view {
    background: rgba(255, 255, 255, 0);
    width: 70%;
    height: 25%;
    color: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-filter: drop-shadow(5px 25px 1px rgb(17, 17, 17));
            filter: drop-shadow(5px 25px 1px rgb(17, 17, 17));
    font-size: 1.5em;
    font-family: "Big Shoulders Stencil Text", cursive;
    text-transform: capitalize;
    letter-spacing: 0.1em;
}

@media (max-width: 550px) {
    .choose-btn {
        font-size: 0.7em;
    }



}
@media (max-width: 350px) {
    .choose-btn {
        font-size: 0.5em;
    }
}

:root {
    --text-color-neon: rgb(235, 69, 180);
    --border-color: #3da7f8;
    --animation-time: 3s;
    --text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 20px var(--text-color-neon),
        0 0 30px var(--text-color-neon), 0 0 40px var(--text-color-neon),
        0 0 55px var(--text-color-neon), 0 0 75px var(--text-color-neon);
}

.neon {
    background-color: transparent;
    display: inline-block;
    /* transform: rotate(-6deg); */
    /* animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder calc(var(--animation-time) * 16) infinite step-end 8s; */

    /* font-family: "Dancing Script", cursive; */
    font-weight: bolder;
}

p {
    /* color: rgb(251, 232, 255); */
}

.neon span {
    font-size: 3.5em;
    letter-spacing: 1rem;
    text-transform: uppercase;
}

p span {
    opacity: 0.33;
    animation: turnOn forwards step-end;
}

p span:nth-child(1) {
    animation-delay: 0.3s;
}

p span:nth-child(2) {
    animation-delay: 0.2s;
}

p span:nth-child(3) {
    animation-delay: 0.1s;
}

p span:nth-last-child(1) {
    animation: turnOn 3.4s forwards step-end,
        flicker 3s infinite step-end 2s;
    animation: turnOn 3.4s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
}


@keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    30% {
        text-shadow: none;
    }
    33% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    35% {
        text-shadow: none;
    }
    37% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}


